<template>
    <div class="active-card">
            <div class="content" v-if="form">
                <div class="head">
                    <vue-scroll>
                        <p class="tit">{{ $L('我的信息') }}</p>
                        <el-form size="small" :model="form"  label-width="60px" label-position="left"  class="info-msg">
                            <el-form-item v-for="(item,index) in form" :key="index" :label="formListLang(item.name)">
                                <el-select v-if="item.type === 'radio'" v-model="info[index]">
                                    <el-option v-for="(i,k) in item.value" :key="k" :label="i" :value="i">{{ i }}</el-option>
                                </el-select>
                                <el-date-picker v-else-if="item.type === 'date'"
                                                v-model="info[index]"
                                                value-format="yyyy-MM-dd"
                                                :placeholder="$L('请选择生日日期')">
                                </el-date-picker>
                                <el-input v-else-if="item.name !== $L('手机号')" v-model="info[index]" value="item.user_value" :placeholder="$L('请输入') +formListLang(item.name)"/>
                                <el-input v-else v-model="info[index]" class="input" type="text" :placeholder="$L('请输入您的手机号码')"/>
                            </el-form-item>
                            <el-form-item :label="$L('验证码')" v-if="authCode == 1" class="code">
                                <el-input v-model="code" class="code" type="text" :style="{ width:'100px' }"/>
                                <el-button class="get-code"
                                           type="primary"
                                           v-on:click="getCodeFunc">
                                    {{ $L('获取验证码') }}</el-button>
                            </el-form-item>
                            <div class="btn-submit">
                                <el-button class="btn" type="primary" @click="submitFunc">{{ $L('立即激活') }}</el-button>
                            </div>
                        </el-form>
                    </vue-scroll>
                </div>
            </div>


    </div>
</template>

<script>
    export default {
        name: "activeCard",
        data () {
            return {
                info:{},
                code:'',
            }
        },
        props : {
            routerData : {
                type : Object,
                default:()=>{
                    return {};
                }
            }
        },
        inject: ['reload'],
        computed:{
            form:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('form')?this.routerData.data.form:{};
                }
            },
            isCreateVip:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('isCreateVip')?this.routerData.data.isCreateVip:'';
                }
            },
            isHasCard:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('isHasCard')?this.routerData.data.isCreateVip:'';
                }
            },
            authCode:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('code')?this.routerData.data.code:'';
                }
            },
        },
        methods : {
            submitFunc() {
                const _self = this;
                let param = [];
                for(let i in _self.form){
                    param[_self.form[i]['fieldname']] = _self.info[i] != undefined ? _self.info[i] : "";
                }
                if (param.user_form_info_flag_mobile === '' || param.user_form_info_flag_mobile == undefined){
                    _self.$message({
                        type: 'info',
                        message: _self.$L('请输入手机号!')
                    });
                    return ;
                }
                if (param.user_form_info_flag_mobile !== '') {
                    let reg = /^1[0-9]{10}$/;
                    if (!reg.test(param.user_form_info_flag_mobile)) {
                        _self.$message.error(_self.$L("手机号码不正确"));
                        return;
                    }
                }
                if (_self.authCode == 1) {
                    if (!_self.code.trim()) {
                        _self.$message({
                            type: 'info',
                            message: _self.$L('请输入验证码!')
                        });
                        return ;
                    }
                }

                let arr={
                    type: 'active_user',
                    param: param,
                    code:_self.code,
                };
                _self.$api('activeCard', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.$message({
                            type: 'success',
                            message: _self.$L('成功领取会员卡!')
                        });
                        _self.$router.push({name:"userCardMobile"});
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.backFunc();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            if (action === 'cancel') {
                                _self.$router.push({name: 'home'});
                            } else {
                                _self.loading = false;
                                _self.showError(_self.$L('网络请求失败'),_self.$L('请刷新页面重试'),'error');
                            }
                        });
                    }
                }, function (error) {
                    _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                        confirmButtonText: _self.$L('重新登录'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                    }).catch(action => {
                        _self.backFunc();
                    });
                });

            },
            getCodeFunc(){
                const _self = this;
                let param = [];
                for(let i in _self.form){
                    param[_self.form[i]['fieldname']] = _self.info[i];
                }
                if (param.user_form_info_flag_mobile === '' || param.user_form_info_flag_mobile == undefined){
                    _self.$message({
                        type: 'info',
                        message: _self.$L('请输入手机号!')
                    });
                }
                if (param.user_form_info_flag_mobile !== '') {
                    let reg = /^1[0-9]{10}$/;
                    if (!reg.test(param.user_form_info_flag_mobile)) {
                        _self.$message.error(_self.$L("手机号码不正确"));
                        return;
                    }
                }

                _self.loading = true;
                let arr = {
                    send_mobile: 1,
                    mobile: param.user_form_info_flag_mobile,
                };
                _self.$api('sendCode', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;

                        const TIME_COUNT = _self.tableData.limitTime;

                        if (!_self.timer) {
                            _self.count = TIME_COUNT;
                            _self.show = false;
                            _self.timer = setInterval(() => {
                                if (_self.count > 0 && _self.count <= TIME_COUNT) {
                                    _self.count--;
                                } else {
                                    _self.show = true;
                                    clearInterval(_self.timer);
                                    _self.timer = null;
                                }
                            }, 1000)
                        }

                        console.log("false:", _self.show);

                        _self.$message({
                            message: _self.$L('新手机验证码发送成功咯！'),
                            type: 'success'
                        })

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.reload();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

            },
            backFunc() {
                this.$router.push({name: 'userCardMobile'});
            },
            formListLang(val){
                let x;
                switch(val){
                    case '姓名':
                        x = this.$L(' 姓名');
                        break;
                    case '性别':
                        x = this.$L('性别');
                        break;
                    case '生日':
                        x = this.$L('生日');
                        break;
                    case '邮箱':
                        x = this.$L('邮箱');
                        break;
                    case '详细地址':
                        x = this.$L('详细地址');
                        break;
                    case '学历':
                        x = this.$L('学历');
                        break;
                    case '行业':
                        x = this.$L('行业');
                        break;
                    case '收入':
                        x = this.$L('收入');
                        break;
                    case '爱好':
                        x = this.$L('爱好');
                        break;
                    case '手机号':
                        x = this.$L('手机号');
                        break;
                    default:
                        x = this.$L('未知');
                        break;
                }
                return x;
            },
        }
    }
</script>

<style lang="scss" scoped>
    .active-card{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        .content{
            background: #fff;
            .head{
                margin: 20px;
                padding: 15px 0;
                .tit{
                    padding-bottom: 10px;
                }
                .font-grey{
                    color: #999;
                }
                .info-msg{
                    /deep/ .el-form-item__label{
                        font-size: 12px;
                    }
                    /deep/ .el-form-item{
                        margin-bottom: 5px;
                    }
                    /deep/ .el-input__inner{
                        border: none;
                        background: #fff;
                    }
                    /deep/ .el-select ,/deep/ .el-date-editor{
                        width: 100%;
                    }
                    .btn-submit{
                        text-align: left;
                        padding-top: 50px;
                        border-top: 1px solid #eee;
                        .btn{
                            width: 100%;
                        }
                    }
                }
            }
        }

    }
</style>
